.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

.footer{
  background-color: #D1C4E9;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}

.card {
  margin-top: 5rem;
  opacity: 0.75;
  padding: 0;
}

.card:hover{
  position: relative;
  bottom: 8px;
  right: 5px;
  box-shadow: 8px 5px black;
}

.card-header {
  background-color: lightskyblue;
  color: black;
}

.card-body {
  background-color: black;
  color: white;
}

.card a {
  text-decoration: none;
}

#margin-bottom {
  margin-bottom: 5rem;
}

/* body gradient -> #00AAFF(blue) -> #00FF6C(green) */
/* idea -> #2E3192 â†’ #1BFFFF (ocean blue) */
/* default = #7b4397 , #dc2430 (purple to red) */
.navbar-dark {
  background-image: linear-gradient(to right, #00F , #0FF);
  padding: 10px;
}

.navbar-dark .navbar-nav .nav-link {
  color: white;
}

.navbar-dark .navbar-nav .nav-link:hover {
  opacity: 0.5;
  color: white;
}

.navbar-dark .navbar-nav .nav-link.active:hover {
  opacity: 1;
  color: white;
}

.navbar-dark .navbar-nav .nav-link.active, .navbar-dark .navbar-nav .show>.nav-link {
  background-color: rgb(43, 168, 43);
  color: white;
}

.nav-link {
  border-radius: 5px;
  font-weight: 600;
}

#cs50text {
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px black;
}

#loginbutton {
  background-color: blue;
  color: white
}

#loginbutton:hover {
  background-color: rgb(66, 66, 219);
}

@media only screen and (max-width: 767px){
  .nav-link {
    margin-left: -400px;
    margin-right: -400px;
    text-align: center;
    border-bottom: 1px solid rgba(0,0,0,0.05);
  }

  .navbar {
    overflow: hidden;
  }

  #loginbutton {
    margin-left: auto;
  }
}