.image-container {
    margin: 5rem auto;
    margin-bottom: 0;
    background: url('./myphoto.jpeg');
    background-size: cover;
    background-position: center;
    height: 15vw;
    width: 15vw;
    min-width: 150px;
    min-height: 150px;
    border-radius: 20px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    border: 5px solid white;
}

#image {
    margin: 0 auto;
}

.about-info {
    font-size: 1.2rem;
    font-style: italic;
    font-weight: bold;
    background-color: black;
    color: white;
    opacity: 0.7;
    padding: 20px;
    border-radius: 30px;
    margin: 3rem auto;
    box-shadow: 5px 5px 10px black;
}

.about-info h4{
    margin-top: 10px;
}

.spacing {
    margin-left: 175px;
}