.currency-body {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
    text-align: center;
}

.currency-body h1 {
    margin: 0;
    margin-bottom: 3rem;
    font-size: 2.5rem;
    color: gold;
    text-shadow:     0 1px 0 hsl(174,5%,80%),
    0 2px 0 hsl(174,5%,75%),
    0 3px 0 hsl(174,5%,70%),
    0 4px 0 hsl(174,5%,66%),
    0 5px 0 hsl(174,5%,64%),
    0 6px 0 hsl(174,5%,62%),
    0 7px 0 hsl(174,5%,61%),
    0 8px 0 hsl(174,5%,60%),

    0 0 5px rgba(0,0,0,.05),
   0 1px 3px rgba(0,0,0,.2),
   0 3px 5px rgba(0,0,0,.2),
  0 5px 10px rgba(0,0,0,.2),
 0 10px 10px rgba(0,0,0,.2),
 0 20px 20px rgba(0,0,0,.3);
    
}

.currency-input {
    border: 1px solid black;
    border-radius: .3rem;
    padding: .6rem;
    width: 10rem;
    font-size: 1.1rem;
    font-weight: 500;
}

select {
    margin-left: .5rem;
    padding: .6rem;
    font-size: 1.1rem;
    max-width: 320px;
    font-weight: 500;
}

.equals {
    font-weight: bold;
    font-size: 2rem; 
}

.currency-description {
    font-size: 1.5rem;
    background-color: black;
    color: white;
    padding: 1rem;
    opacity: 0.7;
    margin: 2rem 1rem;
    border-radius: 10px;
    font-weight: 500;
}

@media only screen and (max-width: 360px) {
    .currency-input{
        padding: .4rem;
        font-size: 1rem;
    }
    select {
        padding: .3rem;
        max-width: 300px;
        font-size: 1rem;
    }
}

@media only screen and (max-width: 280px) {
    .currency-input{
        padding: .3rem;
        font-size: .7rem;
    }
    select{
        padding: .2rem;
        font-size: .7rem;
        max-width: 250px;
    }
}