@import url(https://fonts.googleapis.com/css2?family=Merienda+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, #00AAFF, #00FF6C);
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

.footer{
  background-color: #D1C4E9;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}

.card {
  margin-top: 5rem;
  opacity: 0.75;
  padding: 0;
}

.card:hover{
  position: relative;
  bottom: 8px;
  right: 5px;
  box-shadow: 8px 5px black;
}

.card-header {
  background-color: lightskyblue;
  color: black;
}

.card-body {
  background-color: black;
  color: white;
}

.card a {
  text-decoration: none;
}

#margin-bottom {
  margin-bottom: 5rem;
}

/* body gradient -> #00AAFF(blue) -> #00FF6C(green) */
/* idea -> #2E3192 â†’ #1BFFFF (ocean blue) */
/* default = #7b4397 , #dc2430 (purple to red) */
.navbar-dark {
  background-image: linear-gradient(to right, #00F , #0FF);
  padding: 10px;
}

.navbar-dark .navbar-nav .nav-link {
  color: white;
}

.navbar-dark .navbar-nav .nav-link:hover {
  opacity: 0.5;
  color: white;
}

.navbar-dark .navbar-nav .nav-link.active:hover {
  opacity: 1;
  color: white;
}

.navbar-dark .navbar-nav .nav-link.active, .navbar-dark .navbar-nav .show>.nav-link {
  background-color: rgb(43, 168, 43);
  color: white;
}

.nav-link {
  border-radius: 5px;
  font-weight: 600;
}

#cs50text {
  color: white;
  font-weight: bold;
  text-shadow: 2px 2px black;
}

#loginbutton {
  background-color: blue;
  color: white
}

#loginbutton:hover {
  background-color: rgb(66, 66, 219);
}

@media only screen and (max-width: 767px){
  .nav-link {
    margin-left: -400px;
    margin-right: -400px;
    text-align: center;
    border-bottom: 1px solid rgba(0,0,0,0.05);
  }

  .navbar {
    overflow: hidden;
  }

  #loginbutton {
    margin-left: auto;
  }
}
.calculator-body {
    display: flex;
    justify-content: center;
}

.calculator-grid {
    margin-top: 5rem;
    margin-bottom: 5rem;
    display: grid;
    grid-template-columns: repeat(4, 4rem);
    grid-template-rows: minmax(5rem, auto) repeat(5, 4rem);
    box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
}

.calculator-grid > button {
    border: 1.5px solid white;
    opacity: 0.75;
    font-weight: 600;
    font-size: 1.5rem;
}

.calculator-grid > button:hover {
    opacity: 0.9;
}

.output {
    grid-column: 1/ -1;
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 10px;
    align-items: flex-end;
    word-wrap: break-wrap;
    word-break: break-all;
}

.output > .previous-operand {
    opacity: 0.75;
}

.output > .current-operand {
    font-size: 1.5rem;
}

.span-two {
    grid-column: span 2;
}
.game-info ol, ul {
    padding-left: 30px;
    list-style: none;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background-color: cyan;
    font-family: 'Merienda One', cursive;
    opacity: 0.7;
    border: 1px solid #999;
    float: left;
    font-size: 45px;
    line-height: 34px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 15vh;
  }
  
  .game-info {
    margin-left: 50px;
  }


  .status{
    background-color: rgb(55, 173, 75);
    color: floralwhite;
    padding: 20px;
    width: 250px;
    text-align: center;
    border-radius: 25px;
    box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
  }

  .game-board {
      background-image: radial-gradient(cyan, blue);
      height: 297.031px;
      width: 297.032px;
      box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
  }

  #dropdown-basic {
    background-color: black;
    opacity: 0.7;
    color: floralwhite;
    margin-top: 20px;
    border-radius: 5px;
    box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
    width: 160px;
  }

  .dropdown-menu {
    padding: 0;
    background-color: black;
    opacity: 0.7;
    border-bottom: 1px solid white;
  }

  .dropdown-item {
    border-top: 1px solid cyan;
    color: white;
    text-align: center;
    width: 160px;
  }

  @media only screen and (max-width: 600px){
    .game{
      flex-direction: column;
      align-items: center;
    }

    .game-info{
      margin: 2rem auto;
    }

    .game-info .dropdown {
      display: flex;
      justify-content: center;
    }

    .dropdown-menu {
      opacity: 0.9;
    }
  }
.main-container {
    background: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.418) ), url(/static/media/weather.add46184.jpg);
    background-size: cover;
    background-position: center;
}

.main-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.search {
    outline: none;
    padding: 20px 7%;
    border-radius: 20px;
    border: none;
    margin-bottom: 5%;
    background: rgba(250, 250, 250, 0.85);
}

.city {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 8%;
    border-radius: 20px;
    background: rgba(250, 250, 250, 0.85);
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
}

.city p {
    margin-top: 10px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    font-weight: bold;
}

.city-temp {
    font-size: 5rem;
    font-weight: bold;
    margin-top: 10px;
    color: #1e2432;
    text-align: center;
}

.city sup {
    font-size: 0.5em;
}

.city-name {
    font-size: 2em;
}

.city-name sup {
    padding: 0.2em 0.6em;
    margin-left: 0.2em;
    border-radius: 30px;
    color: #fff;
    background: #ff8c00;
}

.city-icon {
    margin-top: 10px;
    width: 100px;
    height: 100px;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .search {
        padding: 20px 15%;
    }

    .city {
        padding: 40px 20%;
    }
}

@media only screen and (min-width: 1400px) {
    .main-container {
        height: 91.73vh;
    }
}
.currency-body {
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
    text-align: center;
}

.currency-body h1 {
    margin: 0;
    margin-bottom: 3rem;
    font-size: 2.5rem;
    color: gold;
    text-shadow:     0 1px 0 hsl(174,5%,80%),
    0 2px 0 hsl(174,5%,75%),
    0 3px 0 hsl(174,5%,70%),
    0 4px 0 hsl(174,5%,66%),
    0 5px 0 hsl(174,5%,64%),
    0 6px 0 hsl(174,5%,62%),
    0 7px 0 hsl(174,5%,61%),
    0 8px 0 hsl(174,5%,60%),

    0 0 5px rgba(0,0,0,.05),
   0 1px 3px rgba(0,0,0,.2),
   0 3px 5px rgba(0,0,0,.2),
  0 5px 10px rgba(0,0,0,.2),
 0 10px 10px rgba(0,0,0,.2),
 0 20px 20px rgba(0,0,0,.3);
    
}

.currency-input {
    border: 1px solid black;
    border-radius: .3rem;
    padding: .6rem;
    width: 10rem;
    font-size: 1.1rem;
    font-weight: 500;
}

select {
    margin-left: .5rem;
    padding: .6rem;
    font-size: 1.1rem;
    max-width: 320px;
    font-weight: 500;
}

.equals {
    font-weight: bold;
    font-size: 2rem; 
}

.currency-description {
    font-size: 1.5rem;
    background-color: black;
    color: white;
    padding: 1rem;
    opacity: 0.7;
    margin: 2rem 1rem;
    border-radius: 10px;
    font-weight: 500;
}

@media only screen and (max-width: 360px) {
    .currency-input{
        padding: .4rem;
        font-size: 1rem;
    }
    select {
        padding: .3rem;
        max-width: 300px;
        font-size: 1rem;
    }
}

@media only screen and (max-width: 280px) {
    .currency-input{
        padding: .3rem;
        font-size: .7rem;
    }
    select{
        padding: .2rem;
        font-size: .7rem;
        max-width: 250px;
    }
}
.image-container {
    margin: 5rem auto;
    margin-bottom: 0;
    background: url(/static/media/myphoto.02624102.jpeg);
    background-size: cover;
    background-position: center;
