}

#image {
    margin: 0 auto;
}

.about-info {
    font-size: 1.2rem;
    font-style: italic;
    font-weight: bold;
    background-color: black;
    color: white;
    opacity: 0.7;
    padding: 20px;
    border-radius: 30px;
    margin: 3rem auto;
    box-shadow: 5px 5px 10px black;
}

.about-info h4{
    margin-top: 10px;
}

.spacing {
    margin-left: 175px;
}
