@import url('https://fonts.googleapis.com/css2?family=Merienda+One&display=swap');


   .game-info ol, ul {
    padding-left: 30px;
    list-style: none;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background-color: cyan;
    font-family: 'Merienda One', cursive;
    opacity: 0.7;
    border: 1px solid #999;
    float: left;
    font-size: 45px;
    line-height: 34px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 15vh;
  }
  
  .game-info {
    margin-left: 50px;
  }


  .status{
    background-color: rgb(55, 173, 75);
    color: floralwhite;
    padding: 20px;
    width: 250px;
    text-align: center;
    border-radius: 25px;
    box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
  }

  .game-board {
      background-image: radial-gradient(cyan, blue);
      height: 297.031px;
      width: 297.032px;
      box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
  }

  #dropdown-basic {
    background-color: black;
    opacity: 0.7;
    color: floralwhite;
    margin-top: 20px;
    border-radius: 5px;
    box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
    width: 160px;
  }

  .dropdown-menu {
    padding: 0;
    background-color: black;
    opacity: 0.7;
    border-bottom: 1px solid white;
  }

  .dropdown-item {
    border-top: 1px solid cyan;
    color: white;
    text-align: center;
    width: 160px;
  }

  @media only screen and (max-width: 600px){
    .game{
      flex-direction: column;
      align-items: center;
    }

    .game-info{
      margin: 2rem auto;
    }

    .game-info .dropdown {
      display: flex;
      justify-content: center;
    }

    .dropdown-menu {
      opacity: 0.9;
    }
  }